<template>
  <div class="view column">
    <div class="tool-bar">
      <el-button @click="goTo('home')">放弃编辑</el-button>
      <el-button @click="save" type="primary" class="button">保存</el-button>
    </div>
    <el-collapse v-model="activeNames">
      <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
      <el-collapse-item
        :title="panelDsl.title"
        :name="panelDsl.title"
        v-for="(panelDsl, panelDslIndex) in dslArray"
        :key="'panelDsl'+panelDslIndex"
      >
        <c-table
          class="box-card"
          v-model="currentModelAdd"
          :dsl="panelDsl.rows"
          :rules="rules"
          :tableInfo="panelDsl"
          @eventAvatarUploadSucess="handleAvatarSuccess"
          ref="toptable"
        />
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import { dslArray, rules, modelAddOption, dslArrayAddOption } from "./mock.js";
import Main from "./Main.js";
import { snake2camel } from "./Main.js";

export default {
  name: "HqbjHjNanAdd",
  mixins: [Main],
  mounted() {
    /* 默认展开查询页 */
    this.activeNames = ["男方婚检信息", "女方婚检信息"];

    /* this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    }); */
    /* this.$post("http://127.0.0.1:3000/n/test", {
      name: "asdfasdfsf",
      pwd: "asdfsf"
    }).then(response => {
      console.log(response);
    }); */
  },
  data() {
    return {
      rules,
      dslArray,
      activeNames: [],
      /* add option 点击新增时弹出框的表单*/
      modelAddOption,
      dslArrayAddOption
      /* add option */
    };
  },
  methods: {
    /* 头像上传 */
    handleAvatarSuccess(res, options) {
      console.log("avatarUploadSucess", res, options);
      /* 这个options里可能包含真正需要存储于数据库的图片src的链接（或者其他的信息），重新赋值 */
      this.setCurrentModelAdd(
        Object.assign(this.currentModelAdd, {
          [options.id + "avatar"]: options
        })
      );
    },
    /* 头像上传 */
    /* query panel */
    query() {
      const loading = this.$loading();
      console.log("queryClick", this.modelQuery);
      let doc = this.modelQuery;
      this.$post("/proxy/get", {
        coll: "hqbj_hj_jbzl" /* collection */,
        doc /* document */
      })
        .then(res => {
          if (res.isSuccess) {
            this.tableModelQuery = res.data;
          }
          loading.close();
        })
        .catch(error => {
          this.$message({
            message: "失败，网络故障" + error,
            type: "warning"
          });
          console.log(error);
          loading.close();
        });
    },
    handleDblclick(row, event) {
      console.log(row, event);
      this.setCurrentModel(row);
      /* 打开详情折叠板 */
      this.activeNames = this.dslArray.map(panel => {
        return panel.title;
      });
    },
    /* query panel */
    goTo(command) {
      if (command === "home") {
        this.$confirm("是否放弃编辑?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$router.push({ name: command });
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    save() {
      console.log(this.currentModelAdd);
      function isOk(tables) {
        let count = tables.length;
        let isAllOk = true;
        return new Promise(resolve => {
          tables.map(table => {
            table.valid().then(isSuccess => {
              --count;
              isAllOk = isAllOk && isSuccess;
              if (!isAllOk || count === 0) resolve(isAllOk);
            });
          });
        });
      }
      /* 验证表单是否都符合rule */
      isOk(this.$refs.toptable).then(isAllOk => {
        let doc = snake2camel(this.currentModelAdd);
        if (isAllOk) {
          const loading = this.$loading();
          console.log("this.currentModelAdd");
          /* save */
          this.$post("/proxy/save", {
            coll: "hqbj_hj_jbzl" /* collection */,
            doc /* document */
          }).then(response => {
            if (response.isSuccess) {
              /* 如果是修改，需要更新currentModel */
              if (this.$route.name === "update") {
                this.setCurrentModel(doc);
              }
              loading.close();
              this.$router.push({ name: "home" });
              this.$message({
                showClose: true,
                message: "保存成功",
                type: "success"
              });
            } else {
              this.$message({
                showClose: true,
                message: "保存失败",
                type: "warning"
              });
            }
          });
        } else {
          this.$message({
            showClose: true,
            message: "请按照提示检查输入项",
            type: "warning"
          });
        }
      });
    }
  },
  watch: {
    currentModelAdd(newV, oldV) {
      console.log("model change", newV, oldV);
    }
  },
  components: {
    // CPanelItem: () => import(/* webpackChunkName: "PanelItem" */ "@c/views/PanelItem.vue"),
    CTable: () => import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue")
  }
};
</script>


