let camelObj = ["nBh", "nXm", "nZjlx", "nSfzh", "nCsrq", "nNl", "nLxdh", "nGj", "nMz", "nHjlx", "nYzbm", "nWhcd", "nZy", "nGzdw", "nPeBh", "nSg", "nTz", "nXy1", "nXy2", "nPeXm", "nTxrq", "nJdrq", "nJddw", "nJdys", "vBh", "vXm", "vZjlx", "vSfzh", "vCsrq", "vNl", "vLxdh", "vGj", "vMz", "vHjlx", "vYzbm", "vWhcd", "vZy", "vGzdw", "vPeBh", "vSg", "vTz", "vXy1", "vXy2", "vPeXm", "vTxrq", "vJdrq", "vJddw", "vJdys", "nTxImg", "vTxImg"]
import {
    mapState,
    mapMutations
} from "vuex";

export default {
    computed: {
        ...mapState(["currentModel", "currentModelAdd"]),
    },
    methods: {
        ...mapMutations([
            "setCurrentModel",
            "setCurrentModelAdd"
        ])
    }
}

export function snake2camel(modelSnake) {
    let obj = {}
    return camelObj.map((camel, index) => {
        return obj[camel] = modelSnake[snakeObj[index]];
    })
}