<template>
  <div class="view column">
    <div class="tool-bar">
      <el-button @click="goTo('add')">新增</el-button>
      <el-button @click="goTo('update')" :disabled="Boolean(currentModel&&currentModel.bh)">修改</el-button>
      <el-button @click="goTo('remove')" :disabled="Boolean(currentModel&&currentModel.bh)">删除</el-button>
    </div>
    <el-collapse v-model="activeNames" @change="handleChange">
      <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
      <!-- query -->
      <!-- 需要data methods -->
      <el-collapse-item :title="'查询'" :name="'query'">
        <c-query-panel
          v-model="modelQuery"
          :dsl="dslArrayQuery[0].rows"
          :tableInfo="{tableHeader:tableHeaderQuery,tableModel:tableModelQuery}"
          @queryClick="query"
          @row-dblclick="handleDblclick"
        ></c-query-panel>
      </el-collapse-item>
      <!-- query -->
      <!-- detail -->
      <el-collapse-item
        :title="panelDsl.title"
        :name="panelDsl.title"
        v-for="(panelDsl, panelDslIndex) in dslArray"
        :key="'panelDsl'+panelDslIndex"
      >
        <c-table
          class="box-card"
          v-model="currentModel"
          :dsl="panelDsl.rows"
          :rules="rules"
          :tableInfo="panelDsl"
          ref="topTabe"
          :disabled="true"
        />
      </el-collapse-item>
      <!-- detail -->
    </el-collapse>
  </div>
</template>

<script>
import { dslArray, model, rules } from "./mock.js";
/* query */
import {
  modelQuery,
  dslArrayQuery,
  tableHeaderQuery,
  tableModelQuery
} from "./mock.js";
/* query */
/* addOption */
import { modelAddOption, dslArrayAddOption } from "./mock.js";
/* addOption */
import Main from "./Main.js";

export default {
  name: "Main",
  mixins: [Main],
  mounted() {
    let vm = this;
    /* 当页面打开，暴露自身引用，实现与shell（AppHome页，带有导航的那个页面）通信 */
    parent.postMessage(
      {
        action: "hjhszIframe",
        payload: "success"
      },
      "*"
    );
    /* 页面打开与shell通信暴露自身引用 */
    /* 处理花名册查询的结果 */

    window.addEventListener("message", receiveMessage, false);
    function receiveMessage(event) {
      function takeAction({ data: { action, payload }, source, origin }) {
        console.log(action, payload, source, origin);
        const ACTIONS = {
          openHjhsz() {
            vm.handleDblclick(payload);
          }
        };
        ACTIONS[action] && ACTIONS[action](payload);
      }
      try {
        takeAction(event);
      } catch (error) {
        console.log(error);
      }
    }

    /* 处理花名册查询的结果 */
    /* 默认展开查询页 */
    this.activeNames = ["query"];
    /* this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    });
    this.$post("http://127.0.0.1:3000/n/test", {
      name: "asdfasdfsf",
      pwd: "asdfsf"
    }).then(response => {
      console.log(response);
    }); 
    */
  },
  data() {
    return {
      rules,
      dslArray,
      currentModel: model,
      activeNames: [],
      /* query */
      modelQuery,
      dslArrayQuery,
      tableHeaderQuery,
      tableModelQuery,
      /* query */
      /* add option */
      modelAddOption,
      dslArrayAddOption
      /* add option */
    };
  },
  methods: {
    /* query panel */
    query() {
      const loading = this.$loading();
      console.log("queryClick", this.modelQuery);
      let doc = this.modelQuery;
      this.$post("/proxy/get", {
        coll: "hqbj_hj_jbzl" /* collection */,
        doc /* document */
      })
        .then(res => {
          if (res.isSuccess) {
            this.tableModelQuery = res.data;
          }
          loading.close();
        })
        .catch(error => {
          this.$message({
            message: "失败，网络故障" + error,
            type: "warning"
          });
          console.log(error);
          loading.close();
        });
    },
    handleDblclick(row, event) {
      console.log(row, event);
      this.setCurrentModel(row);
      /* 打开详情折叠板 */
      this.activeNames = this.dslArray.map(panel => {
        return panel.title;
      });
    },
    afterRemove(loading) {
      /* 置空 */
      this.setCurrentModel({});
      /* 展开面板为查询 */
      this.activeNames = ["query"];
      loading.close();
      this.$message({
        showClose: true,
        message: "删除成功",
        type: "success"
      });
      /* 刷新列表 */
      this.query();
    },
    /* query panel */
    goTo(command) {
      if (command === "add") {
        /* 初始化新增model */
        this.setCurrentModelAdd(
          (model => {
            let obj = {};
            for (let key in model) {
              obj[key] = "";
            }
            return obj;
          })(this.currentModelAdd)
        );
        return this.$router.push({
          name: command
        });
      }
      if (command === "update") {
        /* 初始化新增model */
        this.setCurrentModelAdd(
          (model => {
            return { ...model };
          })(this.currentModel)
        );
        return this.$router.push({
          name: command
        });
      }
      if (command === "remove") {
        this.$confirm(
          `确实要删除编号为${this.currentModel.bh}的表单?`,
          "提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        )
          .then(() => {
            const loading = this.$loading();
            /* 发送this.currentModel到后台处理删除请求 */
            console.log(this.currentModel);
            setTimeout(() => {
              this.afterRemove(loading);
            }, 1000 * 2);
          })
          .catch(error => {
            console.log("cancel", error);
          });
      }
    },
    handleChange(activeNames) {
      console.log("activeNames", activeNames, activeNames === this.activeNames);
    }
  },
  watch: {
    model(newV, oldV) {
      console.log("model change", newV, oldV);
    }
  },
  components: {
    // CPanelItem: () => import(/* webpackChunkName: "PanelItem" */ "@c/views/PanelItem.vue"),
    CTable: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue"),
    CQueryPanel: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/QueryPanel.vue")
  }
};
</script>